@charset "UTF-8";
@import "variables";
@import "../../../sass/func";

html, body {
    height: 100%;
}
.content-booking {
  position: relative;
  padding-top: pxToRem(114px);

  width: pxToRem(750px);
  min-height: 100%;
  height: pxToRem(1206px);

  background: url("../images/booking/bg_booking.jpg") no-repeat center;
  background-size: 100% 100%;
  box-sizing: border-box;
  color: #51446b;
  // .corner-mark {
  //   position: absolute;
  //   top: pxToRem(-4px);
  //   right: 0;
  //   display: block;
  //   width: pxToRem(240px);
  //   height: pxToRem(140px);

  //   background: url("../images/booking/corner_mark.png") no-repeat;
  //   background-position: 0 100%;
  //   background-size: contain;
  // }
  .title {
    @include background("../images/booking/title_booking.png", 530px, 88px);
    margin: 0 auto;
  }
  .desc {
    padding: pxToRem(24px) pxToRem(56px) pxToRem(78px);
    font-size: pxToRem(26px);
    line-height: pxToRem(50px);
    text-align: justify;
  }
  .figure {
    @include background("../images/booking/figure.png", 747px, 540px);
    margin: 0 auto;
  }
  section {
    font-size: pxToRem(24px);
    text-align: center;
    padding: pxToRem(30px) 0 pxToRem(24px);
    line-height: pxToRem(42px);
  }
  .font28 {
    display: block;
    font-size: pxToRem(28px);
    padding-bottom: pxToRem(30px);
  }
  strong {
    font-weight: bold;
  }
  .split {
    padding: 0 pxToRem(22px);
  }
}
